/* Sidebar Container */
.sidebar {
    background-color: #1f1f2f;
    color: #fff;
    width: 250px;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    overflow: hidden;
    transition: all 0.3s;
    display: flex;
    flex-direction: column;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.2);
  }
  
  .collapsed {
    width: 80px;
    background-color: #333; /* Change background color when collapsed */
  }
  
  /* Toggle Button */
  .toggleButton {
    background-color: transparent;
    border: none;
    color: #fff;
    font-size: 1.2rem;
    margin: 10px;
    cursor: pointer;
    align-self: flex-end;
    display: block;
  }
  
  /* Logo */
  .logo {
    text-align: center;
    padding: 15px;
    font-size: 1.2rem;
    font-weight: bold;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .logo img {
    width: 40px;
    margin-right: 10px;
  }
  
  .collapsed .logo span {
    display: none;
  }
  
  /* Navigation */
  .navList {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .navItem {
    margin: 0;
  }
  
  .navLink {
    display: flex;
    align-items: center;
    padding: 15px 20px;
    text-decoration: none;
    color: #fff;
    transition: all 0.3s;
  }
  
  .navLink:hover {
    background-color: #444;
    color: #17a2b8;
  }
  
  .navLink span {
    margin-left: 10px;
    font-size: 1rem;
  }
  
  .collapsed .navLink span {
    display: none;
  }
  
  /* Active Link */
  .navLink.active {
    background-color: #17a2b8;
    color: #fff;
  }
  